import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import axios from 'axios';
import firstImg from './img/first.png'
import secondImg from './img/second.png'
import thirdImg from './img/third.png'

var taskData = ""
class App extends Component {
    constructor(){
        super()
        this.state = ({
            content: "",
            proHid:"hidden",
            progress:"",
            prowidth:0,
            creater:"",
        })
        this.getTaskData()
    }
  render() {
    return (
      <div className="App">
          <div style={{backgroundColor:'white',marginLeft:'20px',marginTop:'15px',marginRight:'20px'}}>
              <div style={{width:'100%',overflow:'hidden'}} dangerouslySetInnerHTML={{__html: this.state.content}} />
              {/*<p style={{height:'16px'}}></p>*/}
              {/*<p style={{width:'34px',height:'5px',marginLeft:'12px',backgroundColor:'#4C7CFA'}}></p>*/}
              {/*<p style={{marginLeft:'12px',marginTop:'10px',marginRight:'71px',color:'#2F2F2F',fontSize:'13px',fontWeight:'600'}}>*/}
                  {/*乐语斐讯K3系列路由器，S7系列体脂秤销售任务，全国店员奖励*/}
              {/*</p>*/}
              {/*<p style={{marginLeft:'15px',marginRight:'15px',marginTop:'29px',fontSize:'11px',color:'#2F2F2F',lineHeight:'20px'}}>*/}
                  {/*1.斐讯K2路由器产品奖励 全国销售个人第一名：斐讯K3+悟净（空气净化器）各一台 全国销售个人第二名：斐讯K3C+悟空*/}
                  {/*（空气检测仪）各一台 全国销售个人第三名：斐讯K2P路由器与K2路由器各一台<br/>*/}
                  {/*2.店员核销产品时，需扫描产品包装上的S/N产品条形码，并上传票据照片。 注：以上奖品与PK赛额外奖励一同发放，以卖教售数据为基础，请全国各地子公司宣贯到位。*/}
              {/*</p>*/}
              {/*<div style={{marginTop:'40px',height:'47px',width:'100%'}}>*/}
                  {/*<img src={logo} className="App-logo1" alt="logo" />*/}
                  {/*<img src={logo} className="App-logo2" alt="logo" />*/}
                  {/*<img src={logo} className="App-logo3" alt="logo" />*/}

              {/*</div>*/}
              <div hidden={this.state.proHid}>
              <p style={{marginLeft:'13px',marginTop:'38px',fontSize:'12px',color:'#2F2F2F'}}>{this.state.progress}</p>
              <div style={{marginTop:'10px',width:'100%',position:'relative',height:'5px',paddingTop:'0.1px'}}>
                  <div className="allprogress-p"></div>
                  <div className="progres-p" style={{width:this.state.prowidth}}></div>
              </div>
              </div>
              <div style={{marginTop:'36px',width:'100%',position:'relative',height:'50px'}}>
                  <div style={{position:'absolute',color:'#2F2F2F',fontSize:'12px',marginLeft:'13px',display:'inline',marginTop:'0px'}}>
                      剩余时间<br/>
                      {window.remaintime}
                  </div>

                  <div style={{fontSize:'12px',color:'#2F2F2F',marginRight:'39px',marginTop:'0px',float:'right'}}>
                      发起人<br/>
                      {this.state.creater}
                  </div>
              </div>
          </div>
          <div style={{backgroundColor:'#ECF0F8',marginLeft:'20px',marginTop:'15px',marginRight:'20px'}}>
            <p style={{fontSize:'15px',color:'#2F2F2F'}}>排行榜</p>
              <div style={{width: '100%',overflowX:'scroll',height:'142px',marginTop:'0px',whiteSpace:'nowrap'}}>
                  <div style={{backgroundColor:'white',marginTop:'0px',height:'142px',width:'142px',display:'inline-block'}}>
                          <div style={{float:'left'}}>
                              <div>
                                  <img className="ranking-img" src={firstImg}/>
                                  <p className="ranking-p">第一名</p>
                              </div>

                          </div>
                  </div>
                  <div style={{backgroundColor:'#ECF0F8',height:'142px',width:'4px',display:'inline-block'}}>
                  </div>
                  <div style={{backgroundColor:'white',height:'142px',width:'142px',display:'inline-block'}}>
                      <div style={{float:'left'}}>
                              <img className="ranking-img" src={firstImg}/>
                              <p className="ranking-p">第二名</p>
                      </div>
                  </div>
                  <div style={{backgroundColor:'#ECF0F8',height:'142px',width:'4px',display:'inline-block'}}>
                  </div>
                  <div style={{backgroundColor:'white',height:'142px',width:'142px',display:'inline-block'}}>
                      <div style={{float:'left'}}>
                          <img className="ranking-img" src={firstImg}/>
                          <p className="ranking-p">第三名</p>
                      </div>
                  </div>
              </div>
          </div>
        {/*<p style={{color:'#2F2F2F',fontSize:'15px',marginLeft:'20px',fontWeight:'800'}}>排行榜</p>*/}

      </div>
    );
  }
    getTaskData() {  ///ksb-task/api/v1/task/39ab9c84-7966-44a6-9853-060174f1d3c3
        let url = window.urlhttp + '/ksb-task/api/v1/claim/' + window.taskid
        console.log("111:" + url)
        let tmpthis = this;
        let config = {
            // headers: {'Content-Type': 'application/json'},
            dataType: 'jsonp'
        };  //添加请求头
        axios.get(url,config)
            .then(function (response) {
                taskData = response
                console.log("33333 : " + response.data.task.meta.content);
                console.log("44444 : " + response.data.task.meta.total);
                console.log("55555 : " + response.data.task.meta.progress);
                let type = "hidden"
                let progress = ""
                let proWidth = 0
                if(response.data.task.meta.total != undefined && response.data.task.meta.progress != undefined){
                    type = "",
                    progress = "销售进度："+response.data.task.meta.progress + "/" + response.data.task.meta.total
                    proWidth = (window.screen.width-40-24)*(response.data.task.meta.progress/response.data.task.meta.total)
                }

                console.log("666 : " + type + window.screen.width);
                tmpthis.setState({
                    content:decodeURI(taskData.data.task.meta.content),
                    proHid:type,
                    progress:progress,
                    prowidth:proWidth,
                    creater:taskData.data.task.creator,
                })
            })
            .catch(function (error) {

            });
    }
}

export default App;

