import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import {BrowserRouter, HashRouter,Route, Link, Prompt, Redirect, Switch} from "react-router-dom";
import registerServiceWorker from './registerServiceWorker';
import TaskFinshList from './page/TaskFinshList'
import NativeCallBack from './NativeCallBack';
function GetRequest() {
    var url = window.location.search; //获取url中"?"符后的字串
    var theRequest = new Object();
    if (url.indexOf("?") != -1) {
        var str = url.substr(1);
        var strs = str.split("&");
        for (var i = 0; i < strs.length; i++) {
            theRequest[strs[i].split("=")[0]] = decodeURIComponent(strs[i].split("=")[1]);
        }
    }
    return theRequest;
}
window.urlhttp  = "http://192.168.1.188:8892";
var query = GetRequest();
console.log("taskid: "+query["taskid"])
window.taskid = query["taskid"]
window.remaintime = query["remaintime"]
if(query["taskid"] == undefined){
    window.taskid = ""
    console.log("taskid: "+"1111")
}
if(query["remaintime"] == undefined){
    window.remaintime = ""
    console.log("remaintime: "+"1111")
}
NativeCallBack.click_alert()
ReactDOM.render(
    <HashRouter>
        <Switch>
            <Route exact path="/" component={App}/>
            <Route path="/tasklist" component={TaskFinshList}/>
            <App/>
        </Switch>
    </HashRouter>
    , document.getElementById('root'));
registerServiceWorker();



